import Recommend from "@/views/Recommend";
import Login from "@/components/Login";
import PlayBar from "@/components/PlayBar";
import { computed, ref } from "vue";
import { useStore } from "vuex";
export default {
  name: "App",
  components: {
    Recommend: Recommend,
    Login: Login,
    PlayBar: PlayBar
  },
  setup: function setup() {
    var store = useStore(); // 登录框显示与隐藏

    var loginDialogVisible = computed(function () {
      return store.getters.loginDialogVisible;
    });
    var isShowDesc = ref(false); // 页面滚动

    var winX = null;
    var winY = null;
    window.addEventListener("scroll", function () {
      if (winX !== null && winY !== null) {
        window.scrollTo(winX, winY);
      }
    });

    var disableWindowScroll = function disableWindowScroll() {
      winX = window.scrollX;
      winY = window.scrollY;
    };

    var enableWindowScroll = function enableWindowScroll() {
      winX = null;
      winY = null;
    }; // 登录框显示与隐藏


    if (loginDialogVisible.value) {
      disableWindowScroll();
    } else {
      enableWindowScroll();
    }

    return {
      loginDialogVisible: loginDialogVisible,
      disableWindowScroll: disableWindowScroll,
      enableWindowScroll: enableWindowScroll
    };
  }
};